<script setup lang="ts">
import { defineProps, toRefs } from "vue";
interface props {
  color?: string;
  href?: string | null;
  line?: boolean;
}

const props: props = defineProps({
  color: String,
  href: {
    type: String,
    default: null,
  },
  line: Boolean,
});

const { color, href, line } = toRefs(props);

const link = () => {
  href.value && window.open(href.value);
};
</script>

<template>
  <span
    class="my-a"
    @click="link"
    :style="{
      color: line ? '' : color,
    }"
  >
    <slot></slot>
    <div
      class="line"
      v-if="line"
      :style="{
        background: color,
      }"
    ></div>
  </span>
</template>

<style>
.my-a {
  display: inline-block !important;
  opacity: 0.8;
  transition: color 0.5s linear;
  position: relative;
  cursor: var(--cursor-B);
}
.my-a:hover {
  color: v-bind(color);
}
.my-a:hover .line {
  width: 100%;
}
.my-a .line {
  width: 0%;
  height: 2px;
  transition: width 0.3s linear;
  position: absolute;
  bottom: 0;
  left: 0;
}
</style>
