import { getRandom } from "../../util/util";

const nprogress: HTMLElement = document.createElement("div");
nprogress.className = "nprogress";
nprogress.style.cssText = `
    width:0%;
    height:3px;
    background:var(--themeColor);
    position:fixed;
    top:0;
    left:0;
    transition: width 0.3s ease-in-out;
    z-index:98;
    `;
document.body.appendChild(nprogress);

const start = (): void => {
  nprogress.style.display = "block";
  nprogress.style.width = `${getRandom(30, 90)}%`;
};

const end = (): void => {
  nprogress.style.width = "100%";
  setTimeout(() => {
    nprogress.style.display = "none";
    nprogress.style.width = "0%";
  }, 900);
};

window.$nprogress = {
  start,
  end,
};

export default {
  start,
  end,
};
