<script setup lang="ts">
import { defineProps, toRefs, onMounted, ref } from "vue";
import "../prism/prism";
interface props {
  lang?: string;
}

const props: props = defineProps({
  lang: String,
});
const pre = ref<null | HTMLElement>(null);
const { lang } = toRefs(props);
onMounted(() => {
  const el = pre.value;
  const content =
    lang.value == "html" ||
    lang.value == "vue" ||
    lang.value == "css" ||
    lang.value == "less" ||
    lang.value == "scss"
      ? el.parentNode.host.innerHTML.trim()
      : el.parentNode.host.innerText.trim();
  try {
    const html =
      lang.value == "html" ||
      lang.value == "vue" ||
      lang.value == "css" ||
      lang.value == "less" ||
      lang.value == "scss"
        ? Prism.highlight(content, Prism.languages.html, "html")
        : Prism.highlight(content, Prism.languages[lang.value], lang.value);
    el.children[0].innerHTML = html;
  } catch (error) {}
});
</script>

<template>
  <pre ref="pre">
    <code :class="'language-' + lang">
        <slot></slot>
    </code>
</pre>
</template>

<style lang="scss">
@import url("../prism/prism");
@font-face {
  font-family: "fira-code";
  src: url("../public/font/FiraCode-Medium.ttf");
}
pre {
  border-radius: 4px;
  background: rgb(40, 44, 52) !important;
  text-align: left;
  box-sizing: border-box;
  padding: 10px;
  white-space: normal !important;
  overflow-x: auto;

  code {
    border-radius: 4px !important;
    font-size: 18px !important;
    font-family: "fira-code" !important;
    word-spacing: normal !important;
    word-break: break-all !important;
    word-wrap: break-word !important;
  }
}
* ::selection {
  color: #fff;
  background-color: var(--themeColor);
}

* ::-moz-selection {
  color: #fff;
  background-color: var(--themeColor);
}

*::-webkit-selection {
  color: #fff;
  background-color: var(--themeColor);
}

*::-webkit-scrollbar,
*::-webkit-scrollbar-track-piece {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: var(--themeColor);
  background-image: -webkit-linear-gradient(
    45deg,
    hsla(0, 0%, 100%, 0.6) 25%,
    transparent 0,
    transparent 50%,
    hsla(0, 0%, 100%, 0.6) 0,
    hsla(0, 0%, 100%, 0.6) 75%,
    transparent 0,
    transparent
  );
}

*::-webkit-scrollbar {
  width: 7px;
  height: 6px;
}
</style>
