<script lang="ts" setup>
import {
  NConfigProvider,
  darkTheme,
  lightTheme,
  NMessageProvider,
  NNotificationProvider,
  NDialogProvider,
  NLoadingBarProvider,
} from "naive-ui";
import { reactive, onMounted } from "vue";
import fullScreenLoading from "@cop/loading/fullScreenLoading/index.vue";
import { useStore } from "vuex";
import { getStorage } from "@/util/cache";
const store = useStore();

const initThemeColor = () => {
  const themeColor =
    getStorage("themeColor") == null
      ? store.state.themeColor
      : getStorage("themeColor");

  store.commit("setThemeColor", themeColor);
};
onMounted(() => {
  initThemeColor();
});
</script>

<template>
  <n-config-provider
    :theme="store.state.theme == 'light' ? lightTheme : darkTheme"
    :theme-overrides="{
      common: {
        primaryColor: store.state.themeColor,
        primaryColorHover: store.state.themeColor,
        primaryColorPressed: store.state.themeColor,
        primaryColorSuppl: store.state.themeColor,
      },
    }"
  >
    <n-notification-provider>
      <n-dialog-provider>
        <n-loading-bar-provider>
          <n-message-provider>
            <router-view></router-view>
            <fullScreenLoading />
          </n-message-provider>
        </n-loading-bar-provider>
      </n-dialog-provider>
    </n-notification-provider>
  </n-config-provider>
</template>

<style lang="scss">
@import url("@/styles/reset.scss");
</style>
