import { createStore, useStore as baseUseStore, Store } from "vuex";
import { setStorage } from "../util/cache";
import { InjectionKey } from "vue";

let index: number = 0;

export const key: InjectionKey<Store<State>> = Symbol();

export interface State {
  adminInfo: object;
  isLogin: boolean;
  themeColor: string;
  theme: string;
  routeStack: Array<routerItem>;
}
export interface routerItem {
  canItBeClosed: boolean;
  path: string;
  title: string;
}
export interface adminInfo {
  admin_passWord: string;
  admin_userName: string;
  name: string;
  userType: string;
}

export const store = createStore<State>({
  state: {
    adminInfo: {},
    isLogin: false,
    themeColor: "#409eff",
    theme: "light",
    routeStack: [],
  },
  mutations: {
    setThemeColor(state: State, val: string): void {
      //设置主题色
      document.documentElement.style.setProperty("--themeColor", val);
      state.themeColor = val;
      setStorage("themeColor", val);
    },
    setAdminInfo(state: State, val: adminInfo): void {
      //设置管理员信息
      state.adminInfo = val;
      setStorage("adminInfo", JSON.stringify(val));
    },
    setIsLogin(state: State, val: string | boolean): void {
      //设置登录状态
      val = val == "true";
      state.isLogin = val;
      setStorage("isLogin", val);
    },
    setTheme(state: State, val: string): void {
      //设置主题
      state.theme = val;
      // document.body.className = val;
      setStorage("theme", val);
    },
    setRouterStack(state: State, val: Array<routerItem>): void {
      //直接设置routeStack
      // console.log('valq1=', val);
      state.routeStack = val;
      setStorage("routeStack", JSON.stringify(state.routeStack));
    },
    pushRouterStack(state: State, val: routerItem): void {
      //置入routeItem
      let isRepet = state.routeStack.some((item) => item.title == val.title); //为真 就表示重复了,反之
      if (!isRepet) {
        state.routeStack.push(val);
        if (index != 0) {
          setStorage("routeStack", JSON.stringify(state.routeStack));
        }
        index++;
      }
    },
    filterRouterStack(state: State, index: number): void {
      //筛选置入routeStack
      state.routeStack = state.routeStack.filter((item, i) => i != index);
      setStorage("routeStack", JSON.stringify(state.routeStack));
    },
  },
});

export function useStore() {
  return baseUseStore(key);
}
