import nprogress from "../plugins/nprogress";
import { getStorage } from "../util/cache";
import { store } from "../store";
import {
  createRouter,
  createWebHashHistory,
  createWebHistory,
} from "vue-router";
const routes = [
  {
    path: "/",
    redirect: "/admin/index",
  },
  {
    path: "/admin",
    redirect: "/admin/index",
  },
  {
    path: "/reload",
    name: "reload",
    component: () => import("../views/reload-route.vue"),
  },
  {
    path: "/admin",
    name: "admin",
    component: () => import("../views/admin.vue"),
    meta: {
      title: "主控台",
    },
    children: [
      {
        path: "index",
        name: "index",
        component: () =>
          import("../components/admin/routeContent/components/index/index.vue"),
        meta: {
          title: "主控台",
        },
      },
      {
        path: "blogArticles",
        name: "blogArticles",
        component: () =>
          import(
            "../components/admin/routeContent/components/blogArticles/index.vue"
          ),
        meta: {
          title: "博文总览",
        },
      },
      {
        path: "blogArticleTag",
        name: "blogArticleTag",
        component: () =>
          import(
            "../components/admin/routeContent/components/blogArticleTag/index.vue"
          ),
        meta: {
          title: "博文标签",
        },
      },
      {
        path: "classifyBlogArticle",
        name: "classifyBlogArticle",
        component: () =>
          import(
            "../components/admin/routeContent/components/classifyBlogArticle/index.vue"
          ),
        meta: {
          title: "博文分类",
        },
      },
      {
        path: "comment",
        name: "comment",
        component: () =>
          import(
            "../components/admin/routeContent/components/comment/index.vue"
          ),
        meta: {
          title: "评论列表",
        },
      },
      {
        path: "commentUsers",
        name: "commentUsers",
        component: () =>
          import(
            "../components/admin/routeContent/components/commentUsers/index.vue"
          ),
        meta: {
          title: "评论用户",
        },
      },
      {
        path: "editBlogArticle",
        name: "editBlogArticle",
        component: () =>
          import(
            "../components/admin/routeContent/components/editBlogArticle/index.vue"
          ),
        meta: {
          title: "编辑博文",
        },
      },
      {
        path: "friends",
        name: "friends",
        component: () =>
          import(
            "../components/admin/routeContent/components/friends/index.vue"
          ),
        meta: {
          title: "友链列表",
        },
      },
      {
        path: "friendCheck",
        name: "friendCheck",
        component: () =>
          import(
            "../components/admin/routeContent/components/friends/components/friendCheck/index.vue"
          ),
        meta: {
          title: "友链审核",
        },
      },
    ],
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/Login.vue"),
    meta: {
      title: "登录",
    },
  },
  {
    path: "/:pathMatch(.*)*",
    redirect: "/admin/index",
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const loginState = getStorage("isLogin") == "true"; //获取本地缓存的登录状态
  document.title = to.meta.title;
  nprogress.start();
  if (loginState) {
    if (to.name == "login" || to.name == "admin") {
      next("/admin/index");
      document.title = "主控台";
    } else {
      if (to.name != "404" && to.name != "reload") {
        //404不置入路由栈中 但放行进入404页面
        store.commit("pushRouterStack", {
          canItBeClosed: true,
          title: to.meta.title,
          path: to.path,
        });
      }
      next();
    }
  } else {
    if (to.name == "login") {
      next();
    } else {
      next({ name: "login" });
      document.title = "登录";
    }
  }
});
router.afterEach(() => {
  nprogress.end();
});

export default router;
